<template>
    <div class="clipboard" >
        <Icon type="share" :size="24" class="btn" data-clipboard-text=""/>
    </div>
</template>
<script>
import Clipboard from 'clipboard/src/clipboard.js'
import { confirmInstance } from '@/components/confirm'
export default {
  name: 'Clipboard',
  props: ['text'],
  mounted () {
    this.clipboard = new Clipboard('.btn')
    this.clipboard.on('success', function (e) {
      confirmInstance('', '')

      e.clearSelection()
    })
  },
  beforeDestroy () {
    this.clipboard.destroy()
  }
}
</script>
