<template>
    <div class="login">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="" prop="phone">
    <el-input type="text" v-model="ruleForm.phone" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item label="" prop="password">
    <el-input type="text" v-model="ruleForm.password" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button :loading="loading" type="primary" @click="submitForm('ruleForm')"></el-button>

  </el-form-item>
</el-form>

    </div>
</template>
<script>
import { mapActions } from '@/store/helpers/user'

export default {
  name: 'Login',

  data () {
    var validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        if (this.ruleForm.phone !== '') {
          // this.$refs.ruleForm.validateField('password', function (messge) {
          //   alert(messge) //validateField validatePass2
          // })
          var regTel = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/
          if (!regTel.test(value)) {
            callback(new Error('11'))
          }
        }
        callback()
      }
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        var regPass = /[0-9A-Za-z~!@#$%^&*._?]{6,18}/
        if (!regPass.test(value)) {
          this.$refs.ruleForm.validateField('phone') // 
          callback(new Error('6-18'))
        }
      }
      callback()
    }
    return {
      ruleForm: {
        phone: '',
        password: ''

      },
      rules: {
        phone: [
          { validator: validatePhone, trigger: 'blur' }
        ],
        password: [
          { validator: validatePass, trigger: 'blur' }
        ]

      },
      loading: false
    }
  },
  methods: {
    submitForm (formName) {
      // 
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          this.login(this.ruleForm).finally(() => {
            this.loading = false
          })
        }
      })
    },
    ...mapActions(['login'])

  }
}
</script>
