<template>
    <div class="striped_list" v-if="source.length">
        <slot></slot>
    </div>
</template>
<script>
export default {
  name: 'striped-list',
  props: {
    source: {
      type: Array,
      default: () => []
    }
  }, // 
  methods: {
    stripeList () {
      if (this.$slots.default && this.$slots.default.length) {
        this.$slots.default.forEach((vnode, index) => {
          console.log(vnode.elm)
          const classes = index % 2 === 1 ? 'striped-deep' : 'striped-shallow'
          vnode.elm.classList.add(classes)
        })
      }
    }
  },
  watch: {
    source () {
      this.$nextTick(() => {
        this.stripeList()
      })
    }
  }
}
//  source  []  soltmounted
//   source  dom

//   <striped-list v-if= "source.length">mountedwatchimmediate: true this.$nextTick()dom
// dom this.$nextTick()
</script>
<style lang="less" scoped>
.striped-deep{
   background-color: var(--body-bgcolor);
   &:hover{
       background-color: #dedede;
   }
}
.striped-shallow {
  background-color:#dedede;
}

</style>
